.shopTitle{
    margin-top: 75px;
    text-align: center;
    font-size: 42px;
}
.products{
    width: auto;
    height: auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    place-items: center;

}
.product{

    background-color: transparent;
    border: 2px solid ;
    padding-top: 20px;
    border-radius: 15px;
    width: 250px;
    height: 250px;
    margin: 35px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.product img{
    width: 200px;
}
.product .description{
    text-align: center;
}
.product:hover{
    transition: 0.3s ease-in;
    cursor: pointer;
}
.AddTocart{
    background-color: transparent;
    border: 2px solid rgb(19,19,19);
    min-width: 50px;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 0px;
    padding-bottom: 5px;
    border-radius: 15px;
}
.AddTocart:hover{
    color: white;
    background-color: black;
}